/* Light Theme Styles */

/* General Variables */
:root {
  --foreground-rgb: 0, 0, 0;
  --background-start-rgb: 214, 219, 220;
  --background-end-rgb: 255, 255, 255;
  
  /* Light theme variables */
  --primary-color: #1976d2;
  --secondary-color: #dc004e;
  --background-color: #ffffff;
  --background-alt-color: #f7f7f7;
  --paper-color: #ffffff;
  --text-primary: rgba(0, 0, 0, 0.87);
  --text-secondary: rgba(0, 0, 0, 0.6);
  --divider-color: rgba(0, 0, 0, 0.12);
  --error-color: #f44336;
  --success-color: #4caf50;
  --warning-color: #ff9800;
  
  /* Sidebar specific */
  --sidebar-bg: #f8f9fa;
  --sidebar-item-hover: rgba(0, 0, 0, 0.04);
  --sidebar-item-active: rgba(100, 100, 100, 0.12);
  --sidebar-text: #333;
  --sidebar-icon: #555;
  --sidebar-border: rgba(0, 0, 0, 0.08);
}

/* Base Styles */
body {
  color: var(--text-primary);
  background: var(--background-color);
}

/* Main content styles */
main {
  background-color: var(--background-color);
}

/* Card and container styles */
.MuiCard-root, 
.MuiPaper-root,
.MuiContainer-root,
.MuiDrawer-paper {
  background-color: var(--paper-color) !important;
  color: var(--text-primary) !important;
}

/* Sidebar Styles */
.sidebar {
  background-color: var(--sidebar-bg) !important;
}

.MuiDrawer-paper {
  background-color: var(--sidebar-bg) !important;
  border-right: 1px solid var(--sidebar-border) !important;
  box-shadow: none !important;
}

.sidebar .MuiListItemButton-root {
  color: var(--sidebar-text);
  background-color: transparent;
  border-radius: 8px;
  margin: 4px 8px;
  outline: none !important;
  box-shadow: none !important;
  border: none !important;
}

.sidebar .MuiListItemButton-root:hover {
  background-color: var(--sidebar-item-hover) !important;
  color: #333 !important;
  transform: none !important;
  transition: none !important;
  outline: none !important;
  box-shadow: none !important;
  border: none !important;
}

.sidebar .MuiListItemButton-root.active {
  background-color: var(--sidebar-item-active) !important;
  color: #555 !important;
  font-weight: 500;
}

.sidebar .MuiListItemIcon-root {
  color: var(--sidebar-icon) !important;
  min-width: 40px;
}

.sidebar .MuiListItemText-primary {
  color: var(--sidebar-text) !important;
  font-weight: 500;
}

/* Sidebar Logo */
.sidebar .MuiToolbar-root .MuiTypography-root {
  font-weight: 700;
  color: #555 !important;
  background: none !important;
  background-clip: initial !important;
  -webkit-background-clip: initial !important;
  -webkit-text-fill-color: initial !important;
  font-size: 1.5rem;
  letter-spacing: 0.5px;
}

/* Logout Button */
.sidebar .MuiListItemButton-root:last-child {
  margin-top: 16px;
  border-top: 1px solid var(--sidebar-border);
  border-radius: 0;
  padding-top: 16px;
}

.sidebar .MuiListItemButton-root:last-child .MuiListItemIcon-root {
  color: #d32f2f !important;
}

.sidebar .MuiListItemButton-root:last-child:hover {
  background-color: rgba(0, 0, 0, 0.04) !important;
  box-shadow: none !important;
  transform: none !important;
}

/* Fix for Touch and Focus Effects */
.sidebar *,
.MuiDrawer-paper * {
  outline: none !important;
  -webkit-tap-highlight-color: transparent !important;
  transition: none !important;
  animation: none !important;
}

.sidebar *:focus,
.sidebar *:focus-visible,
.sidebar *:focus-within,
.sidebar *:active,
.MuiDrawer-paper *:focus,
.MuiDrawer-paper *:focus-visible,
.MuiDrawer-paper *:focus-within,
.MuiDrawer-paper *:active {
  outline: none !important;
  border: none !important;
  box-shadow: none !important;
}

/* Remove Ripple Effects */
.sidebar .MuiTouchRipple-root,
.MuiDrawer-paper .MuiTouchRipple-root {
  display: none !important;
} 